import { mapState } from 'vuex'
export default {
    name: 'payBall',
    computed: {
        ...mapState(['userInfo'])
    },
    methods: {
        gotoPay () {            
            let hasLogin = !!this.userInfo
            if (!hasLogin) {    
                this.$tip.toast('请先登录','none')          
                uni.navigateTo({
                    url: '../login/login',
                });
                return
            }
            uni.navigateTo({
                url: '../payPage/payPage'
            })
        }
    },
}
